@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
:target {
  padding-top: 15vh;
  margin-top: -15vh; }

.box {
  background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(50%, #f2f2f2), to(transparent));
  background: -webkit-linear-gradient(left, transparent 0%, #f2f2f2 50%, transparent 100%);
  background: linear-gradient(to right, transparent 0%, #f2f2f2 50%, transparent 100%);
  border-bottom: 3px solid #f2f2f2;
  border-top: 3px solid #f2f2f2;
  margin-bottom: 3px; }

.pretty_particles {
  position: fixed;
  right: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1; }

body {
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  line-height: 1.8;
  background: -webkit-gradient(linear, right top, left top, from(#003cff63), to(#ffc40063));
  background: -webkit-linear-gradient(right, #003cff63, #ffc40063);
  background: linear-gradient(270deg, #003cff63, #ffc40063);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 10s ease infinite;
  animation: AnimationName 10s ease infinite; }
  body section {
    margin-top: 50px; }

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 51%; }
  100% {
    background-position: 0% 50%; } }

@keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 51%; }
  100% {
    background-position: 0% 50%; } }

.btn-lg {
  border-color: #3445fe;
  background-color: #f2f2f2; }
  .btn-lg.round {
    border-radius: 24px; }
  .btn-lg:hover {
    background-color: white; }
  .btn-lg:focus {
    outline: none; }

a:focus,
input:focus,
a:hover,
textarea:focus {
  text-decoration: none;
  outline: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Nunito', sans-serif;
  font-weight: 600;
  color: #32333c;
  margin: 0px; }

h1 {
  font-size: 60px; }

h2 {
  font-size: 36px; }

h3 {
  font-size: 28px; }

h4 {
  font-size: 22px; }

h5 {
  font-size: 18px; }

h6 {
  font-size: 16px; }

ul,
ol {
  margin: 0px;
  padding: 0px;
  list-style-type: none; }

p {
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  color: #8a8fa3;
  margin: 0px; }

.section-title .title {
  font-weight: 700;
  font-size: 45px;
  color: #000;
  padding-bottom: 20px; }

.work-map {
  margin-top: 30px; }
  .work-map div {
    box-shadow: 0px 0px 12px 0px #3445fe; }
    .work-map div div {
      box-shadow: none; }

.work-box {
  overflow-wrap: break-word;
  margin: 5px;
  border: 2px solid #3445fe;
  padding: 30px;
  height: 100%;
  align-items: center; }
  .work-box:hover {
    border: 1px solid #fea634;
    box-shadow: 0px 0px 21px 0px #fea634;
    -webkit-transition: box-shadow 0.3s ease-out 0s;
    transition: box-shadow 0.3s ease-out 0s; }

.scrollBar {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 50vh;
  width: auto;
  direction: rtl; }
  .scrollBar div {
    direction: ltr; }

.footer {
  position: absolute;
  margin-top: 52px;
  background-color: #152136;
  width: 100%; }
  .footer .footer-content {
    padding-top: 25px;
    text-shadow: none; }
    .footer .footer-content ul,
    .footer .footer-content li {
      padding-left: 5px;
      display: inline; }
    .footer .footer-content p {
      color: white;
      font-size: 25px; }
    .footer .footer-content ul,
    .footer .footer-content li,
    .footer .footer-content a {
      font-size: 75px;
      border-radius: 10%; }
    .footer .footer-content a:hover {
      color: white; }

.navigation {
  padding: 1vh;
  background-color: #f2f2f2;
  border-bottom: 2px solid #3445fe;
  text-transform: uppercase;
  font-size: 16px;
  color: #3445fe;
  font-weight: 500; }
  .navigation li {
    margin-left: 30px; }

.header-content {
  height: 80vh;
  width: 100%;
  background-color: #f2f2f2da; }

.image_of_me {
  background-image: url("/img/icons/icon-512x512.png");
  width: 512px;
  height: 512px;
  display: block;
  margin-top: 10px;
  box-shadow: 0px 0px 50px 0px #3445fe; }

@media only screen and (max-width: 480px) {
  .navigation.fixed-top {
    display: none; }
  .header-content {
    margin-top: 0; } }

.about-content.about-title {
  padding-bottom: 15px; }

.about-content:ul {
  padding-top: 15px;
  margin-top: 5px; }

.about-content:li {
  margin-top: 5px; }

.info-icon {
  color: #3445fe; }

.info-text {
  padding-left: 5px; }

.about-skills .skill-item .skill-bar .bar-inner {
  margin: 15px;
  width: 100%;
  height: 5px;
  position: relative; }

.skill-header {
  margin-top: 5px; }

.about-skills .skill-item .skill-bar .bar-inner .progress-line {
  height: 5px;
  border-radius: 20px;
  background-color: #fea634;
  -webkit-transition: width 2s ease-out 2s;
  transition: width 2s ease-out 2s;
  width: 0%; }

.bar-inner {
  border-radius: 20px;
  background-color: #3445fe;
  width: 0%; }

.skill-stars {
  float: right; }

.contact-form {
  text-align: center;
  margin-top: 30px; }
  .contact-form input, .contact-form textarea {
    width: 60%;
    height: 55px;
    border-radius: 5px;
    border: 1px solid #ddd;
    padding: 0 25px;
    font-size: 16px;
    color: #8a8fa3; }
  .contact-form .placeholder {
    opacity: 1;
    color: #8a8fa3; }
  .contact-form :focus {
    border-color: #fea634; }
  .contact-form textarea {
    height: 200px;
    padding-top: 10px; }

